$(document).ready(function(){
  var options = {
  slidesToShow: 1,
  slidesToScroll: 1,
  prevArrow: false,
  nextArrow: false,
  dots: true,
  appendDots: $('.exclusive__bottom'),

  responsive: [
  {
    breakpoint: 640,
    settings: {
        autoplay: true,
        autoplaySpeed: 8000,
         // appendDots: ,
    }
  }]
};


 var options2 = {
  slidesToShow: 3,
  slidesToScroll: 1,
  adaptiveHeight: true,
  prevArrow: $('.prev-arrow'),
  nextArrow: $('.next-arrow'),
 

  responsive: [
  {
  	breakpoint: 980,
  	settings: {
  		slidesToShow: 2
  	}
  },

  {
    breakpoint: 640,
    settings: {
        // autoplay: true,
        // autoplaySpeed: 3000,
        slidesToShow: 1
    }
  }]
};

  $('.exclusive-slider').slick(options);

  $('.feedback__slider').slick(options2)
  .on("setPosition",function(e,t){t.$slides.css("height",t.$slideTrack.height()+"px")});		


function addHeight(slider, slide){
	var heig = $(slider).find('.slick-track').height(); 
	$(slider).find(slide).css('height', heig + 'px');  
}		
 
	addHeight('.feedback__slider', '.feedback__slide');

	$(window).resize(function(){
		addHeight('.feedback__slider', '.feedback__slide');
	});



   $(".go-to").on("click",function(e){
    e.preventDefault();
    var o=$(this).attr("href");
    if($(o).length){
      var s=$(o).offset().top;
      $("body,html").stop().animate({
        scrollTop:s
      },1500)
    } else console.warn("ID don't search!")
    });

   $('.calculator__list-handler').on('click', function(){
    $(this).find('.calculator__sublist').fadeToggle(450);
    $(this).find('.calculator__arrow').toggleClass('rotate-arrow');
   });

   $('.calcularor__list-item-breed, .calcularor__list-item-type').on('click', function(){
    var text = $(this).text();
    var handler = $(this).closest('.calculator__list-handler')[0].childNodes[0];
    handler.nodeValue = text;
    
   });


   $('.calculator-popup__list-handler').on('click', function(){
    $(this).find('.calculator-popup__sublist').fadeToggle(450);
    $(this).find('.calculator-popup__arrow').toggleClass('rotate-arrow');
   });

   $('.calcularor-popup__list-item-breed, .calcularor-popup__list-item-type').on('click', function(){
    var text = $(this).text();
    var handler = $(this).closest('.calculator-popup__list-handler')[0].childNodes[0];
    handler.nodeValue = text;
    
   });



  $(function(){
	$(document).mouseup(function (e){ // событие клика по веб-документу
		var div = $(".calculator__sublist, .calculator-popup__sublist"); // тут указываем ID элемента
		if (!div.is(e.target) // если клик был не по нашему блоку
		    && div.has(e.target).length === 0 && !$('.calculator__list-handler').is(e.target)
			&& !$('.calculator-popup__list-handler').is(e.target))
		     { // и не по его дочерним элементам
			div.fadeOut(400);
			$('.calculator__arrow, .calculator-popup__arrow').removeClass('rotate-arrow');// скрываем его
		}
	});
});
	

  function sliderEvent (event, ui, measure) {
    if(this.id === 'calculator__range-width') {
      measure = " мм"
    } else if (this.id === 'calculator__range-length' || 'calculator__range-wideness') {
      measure = " м"
    }
    $(this).find('.ui-slider-handle').attr("data-text",ui.value + measure);
}



 $( function() {
    $( "#calculator__range-width" ).slider({
      range: "min",
      value: 0,
      min: 0,
      animate: 'fast',
      max: 150,
      slide: sliderEvent
     
  
    }).find(".ui-slider-handle").attr("data-text",$( "#calculator__range-width" ).slider("values",0));


    $( "#calculator__range-length" ).slider({
      range: "min",
      value: 0,
      animate: 'fast',
      min: 0,
      max: 300,
      slide: sliderEvent
  
    }).find(".ui-slider-handle").attr("data-text",$( "#calculator__range-length" ).slider("values",0));


    $( "#calculator__range-wideness" ).slider({
      range: "min",
      value: 0,
      min: 0,
      animate: 'fast',
      max: 100,
      slide: sliderEvent
  
    }).find(".ui-slider-handle").attr("data-text",$( "#calculator__range-wideness" ).slider("values",0));

    $( "#calculator-popup__range-width" ).slider({
      range: "min",
      value: 0,
      min: 0,
      animate: 'fast',
      max: 150,
      slide: sliderEvent
     
  
    }).find(".ui-slider-handle").attr("data-text",$( "#calculator-popup__range-width" ).slider("values",0));


    $( "#calculator-popup__range-length" ).slider({
      range: "min",
      value: 0,
      animate: 'fast',
      min: 0,
      max: 300,
      slide: sliderEvent
  
    }).find(".ui-slider-handle").attr("data-text",$( "#calculator-popup__range-length" ).slider("values",0));


    $( "#calculator-popup__range-wideness" ).slider({
      range: "min",
      value: 0,
      min: 0,
      animate: 'fast',
      max: 100,
      slide: sliderEvent
  
    }).find(".ui-slider-handle").attr("data-text",$( "#calculator-popup__range-wideness" ).slider("values",0));
    
  });

 	$('.feedback__slider-star:last-child').addClass('no-filter');
 	$('.feedback__slider-star').on('click', function(){
 		$(this).addClass('no-filter');
 		$(this).nextAll().addClass('no-filter');
 		if($(this).hasClass('no-filter')) {
 			$(this).prevAll().removeClass('no-filter');
 		}
 	});


$('.header__search-input').on('focus', function(){
  $(this).closest('.header__search').css('border-bottom', "1px dashed");
})

$('.header__search-input').on('blur', function(){
  $(this).closest('.header__search').css('border-bottom', "1px solid");
})

$('.burger-menu__wrap').on('click', function(){
  $(this).addClass('visible');
  $('.header__before').css('left', '0');
  $('.header__middle').addClass('active');
  setTimeout(function(){
    $('html').css('position', 'fixed').css('width', "100%");
  }, 200);
  $('.header__middle').animate({left: 0}, 450, function(){
    // alert("Finish!");
  })
});

$('.burger__close-wrap, .header__menu-link').on('click', function(){
  $('.burger-menu__wrap').removeClass('visible');
   $('.header__before').css('left', '-1000%');
  $('.header__middle').removeClass('active');
  setTimeout(function(){
    $('html').css('position', 'static')
  }, 200);
  setTimeout(function(){
  	 $('.header__middle').animate({left: '-150%'}, 450, function(){
    // alert("Finish!");
  	})
  	}, 750);
 
});


  $('.popup-with-move-anim').magnificPopup({
    type: 'inline',

    fixedContentPos: false,
    fixedBgPos: true,

    overflowY: 'auto',

    closeBtnInside: true,
    preloader: false,
    
    midClick: true,
    removalDelay: 300,
    mainClass: 'my-mfp-slide-bottom'
  });


})

